
.main-nav__list {
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 0 auto;
  padding: 0;
  text-transform: uppercase;
}

@media (max-width: @mobile-width-only) {
  .main-nav--closed .site-list__item {
    display: none;
  }

  &--active {
    display: block;
  }
}

.main-nav--opened .main-nav__wrapper {
  position: absolute;
  top: 66px;
  right: 0;
  left: 0;
  z-index: 10;
  min-height: 159px;
  background-color: @white;
}

@media (min-width: @tablet-width) {
  .main-nav__wrapper {
    display: flex;
    align-items: center;
    justify-content: right;
  }

  .main-nav__list {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    margin: 0;
    padding: 0;
  }
}

@media (min-width: @desktop-width) {
  .main-nav {
    z-index: 2;
  }

  .main-nav__list {
    z-index: 1;
    gap: 25px;
  }
}
